<template>
  <Navbar/>
  <h1 class="text">Üdv <strong class="username-roles">{{ currentUser?.profile?.username }}</strong>, a Wolimby Account-ban, a lenti ikonok közül az egyikre kattintva átírányítás történik meg.</h1>
  <div class="project">
    <div class="project-icon 1">
      <a :href="domain1"><img src="https://i.imgur.com/N72oZnx.png" alt="domain1"></a>
    </div>
    <div class="project-icon 3">
      <a :href="domain2"><img src="https://i.imgur.com/SsMjPCz.png" alt="domain3"></a>
    </div>
  </div>
</template>
  
<script>
import { mapGetters, mapActions } from 'vuex';
import { userRoles } from '@/assets/js/functions.js';
import Navbar from '@/components/Navbar';
import '@/assets/scss/access.scss';

export default {
  name: 'Access',
  components: {
    Navbar
  },

  data() {
    return {
      domain1: `http://${location.hostname.split('.').slice(-2).join('.')}:1000`,
      domain3: `https://media.${location.hostname.split('.').slice(-2).join('.')}`,
      domain2: `http://shortify.${location.hostname.split('.').slice(-2).join('.')}:3000`,
      domain4: `https://cloud.${location.hostname.split('.').slice(-2).join('.')}`,
      domain5: `https://games.${location.hostname.split('.').slice(-2).join('.')}`,
    }
  },

  computed: {
    ...mapGetters(['currentUser']),
  },

  methods: {
    ...mapActions(['getCurrentUser']),

    checkUserRoles() {
      userRoles(this.currentUser.profile?.roles);
    },
  },

  async created() {
    await this.getCurrentUser();
    this.checkUserRoles();
  },
}
</script>
  